import Vue from "vue";
import Vuex from  "vuex";
import mutations from "./mutations";
import actions from "./actions";

Vue.use(Vuex);

const store=new Vuex.Store({
    state:{
        topiclist:[
            {name:"all",desc:"全部",},
            {name:"good",desc:"精华",},
            {name:"share",desc:"分享",},
            {name:"ask",desc:"问答",},
            {name:"job",desc:"招聘",},
            {name:"dev",desc:"测试",}
          ],
          isShow:false,//默认不显示actionsheet菜单
          isLogined:false,//表示默认未登录状态
          //登录后的菜单
          loginedMenus:{
            create:"发表新话题",
            center:"个人中心",
            msgs:"消息中心",
            logout:"退出"
          },
          //未登录的菜单
          unLoginedMenus:{
            login:"登录"
          },
          loginname:"",//保存用户名称
          accesstoken:"",//保存用户令牌
          author_id:"", //保存用户id
          count:""
    },
    //状态数据计算属性
    getters:{
        //根据登录状态显示不同菜单(登录前登陆后)
        moreMenus(state){
            //判断用户是否登录
            //isLogined=true表示已登录
            if(state.isLogined){
              return state.loginedMenus;//显示登录后菜单
            }
            return state.unLoginedMenus;//未登录后菜单
        },
        //处理分类显示汉字
        filterTab(state){
          return function(tab){
            for (let i = 0; i < state.topiclist.length; i++) {
              const element = state.topiclist[i];
              if(element.name==tab){
                return state.topiclist[i].desc;
              }
            }
          }
        }
    },
    mutations,
    actions
});
export default store;


