const mutations={
    //修改isShow的值
    mutationsIsShow:function(state,payload){
        //payload表示接收到的修改后的值
        state.isShow=payload
    },
    //修改登录后的状态
    mutationsLogined:function(state){
        state.isLogined=true
    },
    //保存登录后的用户名
    mutationsLoginname:function(state,payload){
        state.loginname = payload;
    },
    mutationsAccesstoken:function(state,payload){
        state.accesstoken = payload;
    },
    //清空loginname、accesstoken、isLogined的值
    mutationsClear:function(state){
        state.loginname="";
        state.accesstoken="";
        state.isLogined=false;
    },
    mutationsAuthorId:function(state,payload){
        state.author_id=payload
    }
}
export default mutations


