import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from "../components/Home.vue"
import TopicList from "../components/TopicList.vue"
import Topic from "../components/Topic.vue"
import Center from "../components/Center.vue"
import Msgs from "../components/Msgs.vue"
import Logout from "../components/Logout.vue"
import Login from "../components/Login.vue"
import User from "../components/User.vue"
import Create from "../components/Create.vue"
import Edit from "../components/Edit.vue"


Vue.use(VueRouter);

const routes=[
  // {
  //   path:"/",
  //   component:Home
  // },
  //默认加载到主题分类的全部内容
  {
    path:"/",
    redirect:"/topiclist/all"
  },
  //主题列表（对应分类加载）
  {
    path:"/topiclist/:name",
    name:"topiclist",
    component:TopicList,
    props:true
  },
  //主题详情（id值）
  {
    path:"/topic/:id",
    name:"topic",
    component:Topic,
    props:true
  },
  {
    path:"/center",
    name:"center",
    component:Center
  },
  {
    path:"/msgs",
    name:"msgs",
    component:Msgs
  },
  {
    path:"/logout",
    name:"logout",
    component:Logout
  },
  {
    path:"/login",
    name:"login",
    component:Login
  },
  {
    path:"/user/:loginname",
    name:"user",
    component:User,
    props:true
  },
  {
    path:"/create",
    name:"create",
    component:Create,
    props:true
  },
  {
    path:"/topic/:id/edit",
    name:"edit",
    component:Edit,
    props:true
  }
]
const router=new VueRouter({
  routes
})
export default router



