
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Card from 'vux/src/components/card/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapState} from "vuex"
export default {
name:"topic",
props:["id"],
data() {
return {
topics:"",
reply_content:"",//回复帖子的内容
reply_content2:"",//回复评论的内容
isShowPopup:false, //默认弹出框隐藏
reply_id:"", //评论的id

}
},
computed:{
...mapState(["isLogined"])
},
components:{
CellBox,
Group,
Flexbox,
FlexboxItem,
Card,
XTextarea,
XButton,
Popup
},
directives:{
TransferDom
},

beforeRouteEnter (to, from, next) {
next(function(vm){
vm.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function(res){
if(res.data.success){
vm.topics=res.data.data;
}
})
})
},
beforeRouteUpdate (to, from, next) {
var app=this;
this.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){
if(res.data.success){
app.topics=res.data.data;
}
})
next();
},
methods: {

upOrDown(reply_id){
var app=this;
this.$http.post("/reply/"+reply_id+"/ups",{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
console.log(res.data);
if(res.data && res.data.success){//表示点赞或者是取消点赞成功
var reply;

for (let i = 0; i < app.topics.replies.length; i++) {
if(app.topics.replies[i].id==reply_id){



reply = app.topics.replies[i];
break;
}
}
if(res.data.action=="up"){//表示点赞成功

reply.ups.push(app.$store.state.author_id);
}
if(res.data.action=="down"){//表示取消点赞



var index=reply.ups.indexOf(app.$store.state.author_id);
reply.ups.splice(index,1);
}
}
})
},

collect(){
var app=this;
this.$http.post("/topic_collect/collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topics.id
}).then(function(res){
console.log(res.data)
if(res.data.success){//返回true表示收藏成功，手动修改is_collect的值为true
app.topics.is_collect=true
}
})
},

decollect(){
var app=this;
this.$http.post("/topic_collect/de_collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topics.id
}).then(function(res){
console.log(res.data);
if(res.data.success){//返回false表示取消收藏，手动修改is_collect的值为false
app.topics.is_collect=false;
}
})
},

handlePopup(reply_id,reply_loginname){
this.isShowPopup=true;
this.reply_id=reply_id;
this.reply_content2="@"+reply_loginname+" "+this.reply_content;
},
handlehide(){
this.reply_content2="";
},

handleReply(replytype){
var app=this;
var params={
accesstoken:this.$store.state.accesstoken,
content:this.reply_content
}
if(replytype==2){
params.reply_id = this.reply_id;
params.content=this.reply_content2
}
this.$http.post("/topic/"+this.topics.id+"/replies",params).then(function(res){
console.log(res.data);
if(res.data.success){
var reply={
"id": app.reply_id,
"author": {
"loginname": app.$store.state.loginname,
"avatar_url": "https://avatars.githubusercontent.com/u/84436010?v=4&s=120"
},
"content": params.content,
"ups": [],
"create_at": "2021-05-23T15:06:02.504Z",
"reply_id": null,
"is_uped": false
}
app.topics.replies.push(reply);
app.reply_content="";
app.isShowPopup=false;
}
})
},
},
}



