
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState,mapGetters} from "vuex";
export default {
name:"topiclist",
props:["name"],
data() {
return {
listData:[],
page:1,//页码，默认第一页
limit:20,//每一页显示的个数
pullUpConfig:{//设置加载配置信息
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
pulldownConfig:{
content: '下拉刷新数据',
downContent: '松开刷新数据',
upContent: '下拉刷新',
loadingContent: '刷新中...'
}
}
},
computed:{
...mapState(["topiclist"]),
...mapGetters(["filterTab"])
},
components:{
Cell,
Group,
Scroller,
Tab,
TabItem
},


beforeRouteEnter (to,from,next){

next(function(vm){
vm.$http.get("/topics",{
params: {
tab: to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(response){
console.log(response)
if(response.data.success){
vm.listData = response.data.data;
}
})
})
},

beforeRouteUpdate(to,from,next) {
var app=this
this.$http.get("/topics",{
params: {
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
console.log(response)
if(response.data.success){
app.listData = response.data.data;
}
})
next();
},
methods: {

handelTab(index){




this.$router.push("/topiclist/"+this.topiclist[index].name)
},

refreshData(){
var app=this;
this.$http.get("/topics",{
params: {
tab: this.name,
page:1,
limit:this.limit
}
}).then(function(response){

if(response.data.success){
app.listData = response.data.data;

app.$refs.pullup.donePulldown();
app.page=1;
}
})
},

loadMoreData(){
var app=this;
this.page=this.page+1;
this.$http.get("/topics",{
params: {
tab: this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){

if(response.data.success){
for(var i=0;i<response.data.data.length;i++){
app.listData.push(response.data.data[i]);
}

app.$refs.pullup.donePullup();
}
})
}
},
}



